<style>
 @import "../assets/css/background_broadcrumb.css";
</style>
<template>
  <div>
    <div class="cover_about">
          <v-img :src="`${topic.images[0].link}`" cover v-for="(topic,index) in getHeader" :key="index">
            <v-overlay absolute class="bottom_card">
                <v-container>
                <v-card class="card_background" flat color="transparent" left>
                        <v-card-title class="title"><span> {{topic.title}}</span></v-card-title>
                        <v-card-subtitle class="desc">
                            <p>
                               {{topic.full_text}}
                            </p>
                        </v-card-subtitle>
                   </v-card>
                   <v-breadcrumbs :items="items2" class="broadcrumbs"></v-breadcrumbs>
                </v-container>
              
            </v-overlay>
          </v-img>
     
    </div>
    <section class="intro">
      <IntroTopic></IntroTopic>
    </section>
   
    <section class="experts_backgroundDark">
      <ExperiencesList></ExperiencesList>
    </section>
     <section class="vission_mission">
       <MissionList></MissionList>
     </section>
     <section class="sectors">
       <SectorsList></SectorsList>
     </section>
   </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";

import SectorsList from '@/components/SectorsList2.vue';
import MissionList from '@/components/MissionList.vue';
import ExperiencesList from '@/components/ExperiencesList.vue';
import IntroTopic from '@/components/IntroTopic.vue';


export default{
 name:"AboutView",
 components:{
    IntroTopic,
    ExperiencesList,
    MissionList,
    SectorsList,
 },
  data(){
      return{
         items2: [
            {
               text: 'Home',
               disabled: false,
               href:'/',
            },
            {
               text: 'About',
               disabled: false,
               href:'/about',
            },
         ]
      }
    },  
   methods: {
    ...mapActions(["fetchTopics"]),
  },
  computed:{
    ...mapGetters(["getHeader"]),
    
  },
  mounted() {
   let aboutHeader={
    topic_type_id:20,
    cat:"header"
   }
   this.fetchTopics(aboutHeader);
 },
 }

</script>