<style scoped>
  @import "../assets/css/intro.css";
</style>
<template>
  <div>
    <v-container>
        <v-row justify="center">
        
        <v-col cols="6">
            <v-card class="topic_about" flat>
              
                <v-card-title class="title_card">
                    <p>{{getIntro[0].title}}
                    </p>
                </v-card-title>
                <v-card-subtitle class="desc_card">
                    <p>
                     
                       {{getIntro[0].full_text}}
                    </p>
                </v-card-subtitle>
            </v-card>
        </v-col>
        <v-col cols="6">
          <div class="img_top">
            <v-img :src="require('@/assets/images/about/engineer-yellow-points.png')" max-width="674"  max-height="650" contain> 
            </v-img>
          </div>
       </v-col>
     </v-row>
     <v-img :src="require('@/assets/images/rect-yellow.png')" max-width="520" max-height="119" class="rect_yellow"></v-img>
     </v-container>
   
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name:"TopicAbout",
  methods: {
    ...mapActions(["fetchTopics"]),
  },
  computed:{
    ...mapGetters(["getIntro"]),
    
  },
  mounted() {
   let intro={
    topic_type_id:20,
    cat:"intro"
   }
   this.fetchTopics(intro);
  
  },
}
</script>