<template>
<div>
  <v-card class="mission_card mission" flat color="transparent">
    <v-card-title class="title_card">
        <span>{{getMission[0].title}}</span>
    </v-card-title>
    <v-card-subtitle class="desc_card">
        <p>
            {{getMission[0].full_text}}
        </p>
    </v-card-subtitle>
</v-card>
</div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
export default {
 name:"MissionComponent",
 methods: {
    ...mapActions(["fetchTopics"]),
  },
  computed:{
  ...mapGetters(["getMission"]),
  },
  mounted() {
   let values={
    topic_type_id:20,
    cat:"mission"
   }
  this.fetchTopics(values)
  },
}
</script>