<template>
<div>
  <v-card class="mission_card values" flat color="transparent">
    <v-card-title class="title_card">
        <span> {{getValues[0].title}}</span>
    </v-card-title>
    <v-card-subtitle class="desc_card">
        <v-list class="list_values">
              <v-list-item  v-for="(t,index) in splitedStr.length" :key="index" >
                 <v-icon class="fa fa-check marked" v-if="index<splitedStr.length-1"></v-icon>
                    <v-list-item-title > {{index==splitedStr.length-1 ?'':splitedStr[index]}}</v-list-item-title>
              
              
              </v-list-item>
        </v-list>
     </v-card-subtitle>
  </v-card>
</div>
</template>

<script>

import { mapGetters, mapActions } from "vuex";
export default {
 name:"VisionComponent",
  methods: {
    ...mapActions(["fetchTopics"]),
    myFunction: function () {
        const text=this.getValues[0].full_text.split(".");
       
        for(var i=0;i<text.length;i++){
          this.splitedStr=text;
         }
     }
  },
  computed:{
  ...mapGetters(["getValues"]),
  },
  data(){
   return{
   splitedStr:""
   
   }
  
  },
  created(){
   this.myFunction();
  },
  mounted() {
   let values={
    topic_type_id:20,
    cat:"values"
   }
  
   this.fetchTopics(values);
  
  },
}
</script>