<style scoped>
@import "../assets/css/mission_backgrounddark.css";
</style>
<template>
    <div>
        <v-img src="../assets/images/rect-yellow.png" max-width="500" max-height="119" class="rect_yellow"></v-img>
   
         <v-container>
            <v-row>
                <v-col cols="5">
                    <v-img src="../assets/images/about/dark-wind-turbine.png" class="pos_img">
                   
                      <v-card class="card_vission" flat color="transparent">
                        <v-card-title class="title_card">
                            <span>{{getMvv[0].title}}</span>
                        </v-card-title>
                        <v-card-subtitle class="desc_card">
                            <p>
                                   {{getMvv[0].full_text}}   </p>
                        </v-card-subtitle>
                      </v-card>
                    
                    </v-img>
                </v-col>
                <v-col cols="7">
                    <v-layout class="flex-column">
                      <visionComponent></visionComponent>
                      <MissionComponent></MissionComponent>
                      <ValuesComponent></ValuesComponent>
                   </v-layout>
                </v-col>
            </v-row>
            <v-img src="../assets/images/rect-yellow.png" max-width="500" max-height="119" class="rect_yellow2"></v-img>
  
         </v-container>
    </div>
</template>

<script>
import VisionComponent  from '@/components/VisionComponent.vue';
import MissionComponent  from '@/components/MissionComponent.vue';
import ValuesComponent  from '@/components/ValuesComponent.vue';
import { mapGetters, mapActions } from "vuex";
export default {
 name:"MissionList",
 components:{
   VisionComponent,
    MissionComponent,
    ValuesComponent
 },
  methods: {
    ...mapActions(["fetchTopics"]),
   },
   computed:{
    ...mapGetters(["getMvv"]),
   },
  mounted() {
   let mvv={
    topic_type_id:20,
    cat:"mvv"
   }
   this.fetchTopics(mvv)
  },
}
</script>