<template>
  <div>
    <v-card class="mission_card vission" flat color="transparent">
        <v-card-title class="title_card">
            <span>{{getVision[0].title}}</span>
        </v-card-title>
        <v-card-subtitle class="desc_card">
            <p>
                {{getVision[0].full_text}}
            </p>
        </v-card-subtitle>
    </v-card>
 </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
export default {
 name:"VisionComponent",
  methods: {
    ...mapActions(["fetchTopics"]),
  },
    computed:{
    ...mapGetters(["getVision"]),
    },
    mounted() {
    let vision={
    topic_type_id:20,
    cat:"vision"
   }
   this.fetchTopics(vision)
   },
}
</script>